header{
  width: 100%; background-color: #222;

  .inner{
    width: 1180px;
    height: 80px;
    margin: 0 auto;
    position: relative;

    h1{
      position: absolute;
      bottom: 10px;
      left: 0;

      a{
        font: bold 24px/1 'arial';
        color: #fff;
      }
      img{
        height: 30px;
      }
    }

    #gnb{
      position: absolute; bottom: 0; right: 0;
      display: flex;

      li{
        a{
          display: inline-block; padding: 20px 30px;
          font: bold 14px/1 'arial';
          color: #fff;
        }
      }
    }

    .fa-bars{
      font-size: 24px; display: none;
      position: absolute; bottom: 20px; right: 40px;
    }
  }

  &.sub{
    background-color: #fff;
    border-bottom: 1px solid #999;

    .inner{
      h1 a{ color: #222; }
      #gnb li a{
        color: #333;
      }
      .fa-bars{ color: #333; }
    }
  }

  &.main{
    background-color: #222;
    border-bottom: 1px solid #222;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;

    .inner{
      h1 a{ color: #fff; }
      #gnb li a{ color: #fff; }
      .fa-bars{ color: #fff; }
    }
  }
}

@media screen and (max-width: $tablet) {
 header{
  width: 100%;

  .inner{
    width: 100%;

    h1{ bottom: 20px; left: 40px; }
    #gnb{ display: none; }
    .fa-bars{ display: block; }
  }
 }
}